import React, {useEffect} from 'react'
import facebook from '../../assets/socialMedia/socialMedia-24.svg'
import twitter from '../../assets/socialMedia/socialMedia-25.svg'
import instagram from '../../assets/socialMedia/socialMedia-26.svg'
import linkedin from '../../assets/socialMedia/socialMedia-27.svg'
import Item from './Item'

function SocialMedia() {


  useEffect(() => {
    const handleScroll = () => {
      if (
        document.body.scrollTop > 20 ||
        document.documentElement.scrollTop > 20
      ) {
        document.getElementById("socialMedia-scrolled").classList.add("start-[-200px]");
        document.getElementById("socialMedia-scrolled").classList.remove("start-[5px]");
      } else {
        document.getElementById("socialMedia-scrolled").classList.add("start-[5px]");
        document
          .getElementById("socialMedia-scrolled")
          .classList.remove("start-[-200px]");
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);



  return (
    <div id='socialMedia-scrolled' className='fixed mx-1 lg:mx-4 top-[50%] start-[5px] z-[999] overflow-hidden duration-500'>
        <ul className='grid gap-3'>
            <Item name={facebook} />
            <Item name={twitter} />
            <Item name={instagram} />
            <Item name={linkedin} />
        </ul>
    </div>
  )
}

export default SocialMedia