import React from "react";

const LineItems = (props) => {
  return (
    <div className="group lineItem grid items-center duration-300 relative">
      <div className="circuit-bg group-hover:animate-ping w-32 sm:w-40 h-32 sm:h-40"></div>
      <div className={`grid items-center justify-center h-32 sm:h-40 w-32 sm:w-40 relative rounded-[100%] circle-bg${props.id} shadow-xl overflow-hidden duration-300`}>
        <div>
          <div className="absolute  bg-gradient-to-r from-[#0A588Acc] to-[#0A2744cc] h-full w-full top-0 left-0 grid items-center justify-center">
            <p className="px-4 featured text-[12px] lg:text-[16px] text-[#fff] hover:text-[#6DCCDE]  duration-300 cursor-pointer  text-center">
              {props.description}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LineItems;
