import React from "react";
// import { useState, useRef, useEffect } from "react";
import SectionTitle from "./SectionTitle";
import { Slider } from "./Slider";

const ProjectsGallery = () => {
  return (
    <div className="relative my-16 mx-8 lg:mx-52">
      <SectionTitle title=" " description="" />
      <div className="sticky z-[999] grid grid-cols-3 h-20 top-[4.95rem]  ">
        <a
          href="#web"
          className="bg-[#6DCCDE] text-center flex items-center justify-center text-[#fff] font-bold text-xl"
        >
           
        </a>
        <a
          href="#mobile"
          className="bg-[#0A588A] text-center flex items-center justify-center text-[#fff] font-bold text-xl"
        >
            
        </a>
        <a
          href="#design"
          className="bg-[#0A2744] text-center flex items-center justify-center text-[#fff] font-bold text-xl"
        >
           
        </a>
      </div>
      <div className="grid ">
        <div id="web" className=" overflow-hidden bg-[#6DCCDE]">
          <Slider />
        </div>
        <div id="mobile" className="overflow-hidden bg-[#0A588A] ">
          <Slider />
        </div>
        <div id="design" className="overflow-hidden bg-[#0A2744] ">
          <Slider />
        </div>
      </div>
    </div>
  );
};

export default ProjectsGallery;
