import React from 'react'
import { motion } from 'framer-motion'
import { useRef, useEffect, useState } from 'react'
import images from './images';

export const Slider = () => {
    const [width, setWidth] = useState(0);
    const carousel = useRef();

    const window_dir = document.dir ;
    console.log(window_dir)
  
    useEffect(()=>{
      setWidth(carousel.current.scrollWidth - carousel.current.offsetWidth)
    },[]);
  
    return (
      <motion.div initial={window_dir === "rtl" ? {x:"0vw"}:{x:"3vw"}} animate={window_dir === "rtl" ? {x:"3vw"}:{x:"0vw"}} ref={carousel}  className='overflow-hidden px-6 sm:mx-14 sm:py-16'>
        <motion.div className='w-[45rem] sm:w-[120rem] my-5 cursor-grab overflow-hidden' whileTap={{cursor: "grabbing"}}>
          <motion.div drag='x' dragConstraints={window_dir === "rtl" ? {left:0, right:width} : {right:0, left:-width}} className='flex gap-9'>
            {images.map(image => {
              return(
                <div className='text-center' key={image.id}>
                    <motion.div className='mb-5 ' >
                        <img className=' w-full h-full pointer-events-none '  src={image.img} alt=''/>
                    </motion.div>
                    <a className='text-[#fff] sm:text-2xl italic font-bold' href={image.url} target="_blank">{image.url}</a>
                </div>
              );
            })}
          </motion.div>
        </motion.div>
      </motion.div>
    );
  }