import React, { useState } from "react";
import { ChevronLeftIcon, ChevronRightIcon } from "@heroicons/react/solid";

const Testimonials = ({ testimonials }) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const handleNext = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === testimonials.length - 1 ? 0 : prevIndex + 1
    );
  };

  const handlePrev = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === 0 ? testimonials.length - 1 : prevIndex - 1
    );
  };

  return (
    <div className="relative testimonials-bg lg:h-96 my-16">
      {testimonials.map((testimonial, index) => (
        <div
          key={testimonial.id}
          className={`bg-gradient-to-r from-[#0A588Abb] to-[#0A2744bb] py-8  h-full w-full duration-300 ${
            index === currentIndex ? "block" : "hidden"
          }`}
        >
          <div className=" p-8 flex flex-col items-center justify-center text-center">
            <span className=" text-[#fff] text-8xl  duration-500 material-symbols-outlined">
              format_quote
            </span>
            <p className="text-[#fff] px-8 lg:w-1/3">{testimonial.text}</p>
            <div className="mt-8 flex flex-col items-center">
              <img className="h-10 w-10 rounded-full bg-[#fff]" src={testimonial.image} alt={testimonial.name} />
              <p className="text-[#fff] ml-2 text-2xl font-bold">{testimonial.name}</p>
            </div>
          </div>
        </div>
      ))}
      <button
        className="absolute top-1/2 left-0 lg:left-1/4 transform -translate-y-1/2 bg-white p-4 rounded-full shadow-md hover:bg-gray-100"
        onClick={handlePrev}
      >
        <ChevronLeftIcon className="h-6 w-6 text-gray-600" />
      </button>
      <button
        className="absolute top-1/2 right-0 lg:right-1/4 transform -translate-y-1/2 bg-white p-4 rounded-full shadow-md hover:bg-gray-100"
        onClick={handleNext}
      >
        <ChevronRightIcon className="h-6 w-6 text-gray-600" />
      </button>
    </div>
  );
};

export default Testimonials;

