import React from "react";
import Button from "../button/Button";

const SectionTitle = (props) => {
  return (
    <div className="flex justify-center text-center mt-16 mb-8
    ">
      <div className="relative ">
        <div className="flex flex-col items-center justify-center">
          <h3 className=" text-[#0A588A] ">{props.id}. {props.subtitle}</h3>
          <h3 className="text-3xl lg:text-6xl text-[#0A588A] font-black">
             <span className="has-gradient">{props.title}</span>
          </h3>
        </div>
        <p className="lg:text-xl mt-4 text-[#0A588A] mx-16 lg:mx-52">
          {props.description}
        </p>
        <div className="mt-4">
          <Button name="" />
        </div>
      </div>
    </div>
  );
};

export default SectionTitle;
