import React from 'react'

import Button from "../button/Button";


const ServiceItem = (props) => {
  return (
    <div className='opacity-[90%]'>
        <div className="h-full group relative overflow-hidden p-8 lg:px-12 flex flex-col gap-5 justify-center w-full bg-gradient-to-tl from-[#0A2744] via-[#0A2744] to-[#456]">
          <div className="z-[99] flex flex-col gap-5">
            <h3 className="text-[#fff] group-hover:text-[#6DCCDE] duration-500 text-2xl lg:text-3xl font-bold">
              {props.name}
            </h3>
            <p className="sm:w-9/12 lg:w-7/12 text-[#ccc] ">
              {props.description}
            </p>
            <Button name="" url="" />
          </div>

          <img
            className="w-6/12 lg:w-4/12 group-hover:w-7/12 lg:group-hover:w-5/12 absolute z-0 bg-no-repeat bottom-0 end-0 lg:-end-12  duration-500"
            src={props.icon}
            alt="props.icon"
          />
        </div>
    </div>
  )
}

export default ServiceItem